___CSS_LOADER_EXPORT___.push([module.id, "/* \n * SheetCraft AI - Adaptive Clean & Minimal Theme\n * Design System: Refined Zinc / Inter\n * Adapts to Light/Dark via prefers-color-scheme\n */\n\n:root {\n    --bg-color: #ffffff;\n    --app-surface: #ffffff;\n    --card-bg: #ffffff;\n    --card-border: #e4e4e7;\n    --input-bg: #fafafa;\n    --input-border: #d4d4d8;\n    --primary: #2563eb;\n    --primary-hover: #1d4ed8;\n    --primary-bg: #eff6ff;\n    --text-primary: #18181b;\n    --text-secondary: #71717a;\n    --success: #10b981;\n    --success-bg: #ecfdf5;\n    --error: #ef4444;\n    --error-bg: #fef2f2;\n    --radius: 8px;\n    --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);\n    --font-sans: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;\n    --font-mono: 'JetBrains Mono', 'Menlo', monospace;\n}\n\n@media (prefers-color-scheme: dark) {\n    :root {\n        --bg-color: #09090b;\n        --app-surface: #09090b;\n        --card-bg: #18181b;\n        --card-border: #27272a;\n        --input-bg: #18181b;\n        --input-border: #3f3f46;\n        --primary: #3b82f6;\n        --primary-hover: #60a5fa;\n        --primary-bg: rgba(59, 130, 246, 0.1);\n        --text-primary: #fafafa;\n        --text-secondary: #a1a1aa;\n        --success: #34d399;\n        --success-bg: rgba(16, 185, 129, 0.1);\n        --error: #f87171;\n        --error-bg: rgba(239, 68, 68, 0.1);\n        --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.3);\n    }\n}\n\n/* â”€â”€â”€ Reset â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */\n*, *::before, *::after { box-sizing: border-box; }\n\nhtml, body {\n    width: 100%;\n    height: 100%;\n    margin: 0;\n    padding: 0;\n    font-family: var(--font-sans);\n    background-color: var(--bg-color);\n    color: var(--text-primary);\n    -webkit-font-smoothing: antialiased;\n    overflow-x: hidden;\n}\n\n/* â”€â”€â”€ Layout â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */\n#app-body {\n    display: flex;\n    flex-direction: column;\n    height: 100vh;\n}\n\n/* â”€â”€â”€ Header â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */\n.app-header {\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    padding: 14px 20px;\n    background: var(--app-surface);\n    border-bottom: 1px solid var(--card-border);\n    position: sticky;\n    top: 0;\n    z-index: 10;\n}\n\n.brand {\n    display: flex;\n    align-items: center;\n    gap: 10px;\n}\n\n.logo {\n    width: 32px;\n    height: 32px;\n    color: var(--primary);\n    display: flex;\n    align-items: center;\n    justify-content: center;\n}\n\n.brand h1 {\n    font-size: 16px;\n    font-weight: 600;\n    margin: 0;\n    letter-spacing: -0.01em;\n}\n\n.highlight-text {\n    color: var(--primary);\n}\n\n/* Icon button (settings gear) */\n.btn-icon {\n    width: 36px;\n    height: 36px;\n    border: 1px solid var(--card-border);\n    border-radius: var(--radius);\n    background: var(--card-bg);\n    color: var(--text-secondary);\n    cursor: pointer;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    transition: color 0.2s, border-color 0.2s;\n}\n\n.btn-icon:hover {\n    color: var(--text-primary);\n    border-color: var(--text-secondary);\n}\n\n/* â”€â”€â”€ Settings Panel â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */\n.settings-panel {\n    padding: 20px;\n    background: var(--card-bg);\n    border-bottom: 1px solid var(--card-border);\n    animation: slideDown 0.2s ease;\n}\n\n.settings-panel h3 {\n    font-size: 13px;\n    font-weight: 600;\n    text-transform: uppercase;\n    letter-spacing: 0.05em;\n    color: var(--text-secondary);\n    margin: 0 0 16px 0;\n}\n\n.form-group {\n    margin-bottom: 14px;\n}\n\n.form-group label {\n    display: block;\n    font-size: 12px;\n    font-weight: 500;\n    color: var(--text-secondary);\n    margin-bottom: 6px;\n}\n\n.form-input {\n    width: 100%;\n    padding: 8px 12px;\n    font-size: 13px;\n    font-family: var(--font-sans);\n    color: var(--text-primary);\n    background: var(--input-bg);\n    border: 1px solid var(--card-border);\n    border-radius: 6px;\n    outline: none;\n    transition: border-color 0.2s;\n}\n\n.form-input:focus {\n    border-color: var(--primary);\n}\n\nselect.form-input {\n    cursor: pointer;\n    appearance: none;\n    background-image: url(" + ___CSS_LOADER_URL_REPLACEMENT_0___ + ");\n    background-repeat: no-repeat;\n    background-position: right 10px center;\n    padding-right: 30px;\n}\n\n.btn-save {\n    width: 100%;\n    justify-content: center;\n    margin-top: 8px;\n}\n\n/* Model selector row */\n.model-select-wrapper {\n    display: flex;\n    gap: 6px;\n    align-items: center;\n}\n\n.model-select-wrapper .form-input {\n    flex: 1;\n    min-width: 0;\n}\n\n.btn-refresh {\n    width: 32px;\n    height: 32px;\n    flex-shrink: 0;\n    padding: 0;\n}\n\n.btn-refresh:active svg {\n    animation: spin 0.5s ease;\n}\n\n.model-status {\n    font-size: 11px;\n    margin-top: 4px;\n    min-height: 16px;\n    color: var(--text-secondary);\n}\n\n.model-status-ok {\n    color: var(--success);\n}\n\n.model-status-warn {\n    color: var(--error);\n}\n\n/* â”€â”€â”€ Content â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */\n.content-wrapper {\n    flex: 1;\n    padding: 20px;\n    display: flex;\n    flex-direction: column;\n    gap: 16px;\n    width: 100%;\n}\n\n/* â”€â”€â”€ Input Card â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */\n.input-card {\n    background: var(--card-bg);\n    border: 1px solid var(--card-border);\n    border-radius: var(--radius);\n    display: flex;\n    flex-direction: column;\n    box-shadow: var(--shadow-sm);\n    overflow: hidden;\n    transition: border-color 0.2s;\n}\n\n.input-card:focus-within {\n    border-color: var(--primary);\n}\n\ntextarea {\n    width: 100%;\n    min-height: 100px;\n    padding: 14px 16px;\n    background: transparent;\n    border: none;\n    color: var(--text-primary);\n    font-family: var(--font-sans);\n    font-size: 14px;\n    line-height: 1.6;\n    resize: vertical;\n    outline: none;\n}\n\ntextarea::placeholder {\n    color: var(--text-secondary);\n    opacity: 0.7;\n}\n\n.card-footer {\n    padding: 10px 14px;\n    border-top: 1px solid var(--card-border);\n    display: flex;\n    justify-content: flex-end;\n    align-items: center;\n}\n\n/* â”€â”€â”€ Primary Button â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */\n.btn-primary {\n    background: var(--primary);\n    color: white;\n    border: none;\n    padding: 8px 16px;\n    border-radius: 6px;\n    font-size: 13px;\n    font-weight: 500;\n    cursor: pointer;\n    display: flex;\n    align-items: center;\n    gap: 6px;\n    transition: background-color 0.15s, opacity 0.15s;\n}\n\n.btn-primary:hover {\n    background: var(--primary-hover);\n}\n\n.btn-primary:active {\n    opacity: 0.9;\n}\n\n.btn-primary:disabled {\n    opacity: 0.6;\n    cursor: not-allowed;\n}\n\n.btn-primary svg {\n    width: 14px;\n    height: 14px;\n}\n\n/* â”€â”€â”€ Status Pill â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */\n.status-pill {\n    padding: 10px 14px;\n    border-radius: 6px;\n    font-size: 13px;\n    font-weight: 500;\n    display: none;\n    align-items: center;\n    gap: 8px;\n    animation: fadeIn 0.25s ease;\n    border: 1px solid transparent;\n}\n\n.status-pill.info {\n    background: var(--primary-bg);\n    color: var(--primary);\n    border-color: rgba(37, 99, 235, 0.15);\n}\n\n.status-pill.success {\n    background: var(--success-bg);\n    color: var(--success);\n    border-color: rgba(16, 185, 129, 0.15);\n}\n\n.status-pill.error {\n    background: var(--error-bg);\n    color: var(--error);\n    border-color: rgba(239, 68, 68, 0.15);\n}\n\n/* â”€â”€â”€ Spinners â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */\n.spinner {\n    width: 14px;\n    height: 14px;\n    border: 2px solid transparent;\n    border-radius: 50%;\n    border-top-color: currentColor;\n    border-right-color: currentColor;\n    animation: spin 0.7s linear infinite;\n}\n\n.btn-spinner {\n    width: 14px;\n    height: 14px;\n    border: 2px solid rgba(255, 255, 255, 0.3);\n    border-radius: 50%;\n    border-top-color: white;\n    animation: spin 0.7s linear infinite;\n    display: inline-block;\n}\n\n/* â”€â”€â”€ Debug Section â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */\n#debug-section {\n    margin-top: auto;\n    padding-top: 16px;\n}\n\ndetails {\n    background: transparent;\n    border: none;\n    border-radius: var(--radius);\n}\n\nsummary {\n    padding: 6px 0;\n    cursor: pointer;\n    font-size: 12px;\n    font-weight: 500;\n    color: var(--text-secondary);\n    display: flex;\n    align-items: center;\n    gap: 6px;\n    list-style: none;\n    user-select: none;\n    transition: color 0.2s;\n}\n\nsummary::-webkit-details-marker {\n    display: none;\n}\n\nsummary:hover {\n    color: var(--text-primary);\n}\n\ndetails[open] summary svg.chevron {\n    transform: rotate(180deg);\n}\n\nsvg.chevron {\n    transition: transform 0.2s;\n    opacity: 0.6;\n}\n\ndetails[open] summary {\n    margin-bottom: 6px;\n}\n\npre {\n    margin: 0;\n    padding: 12px;\n    background: var(--input-bg);\n    color: var(--text-secondary);\n    font-family: var(--font-mono);\n    font-size: 11px;\n    overflow-x: auto;\n    border: 1px solid var(--card-border);\n    border-radius: 6px;\n    line-height: 1.5;\n    white-space: pre-wrap;\n    word-break: break-all;\n}\n\n/* â”€â”€â”€ Sideload Screen â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */\n.sideload-container {\n    height: 100vh;\n    display: flex;\n    flex-direction: column;\n    align-items: center;\n    justify-content: center;\n    background: var(--bg-color);\n    color: var(--text-secondary);\n    gap: 16px;\n}\n\n.sideload-container .spinner {\n    width: 28px;\n    height: 28px;\n    border-width: 3px;\n}\n\n.sideload-container h2 {\n    font-size: 14px;\n    font-weight: 500;\n    margin: 0;\n}\n\n/* â”€â”€â”€ Keyframes â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */\n@keyframes fadeIn {\n    from { opacity: 0; transform: translateY(4px); }\n    to { opacity: 1; transform: translateY(0); }\n}\n\n@keyframes slideDown {\n    from { opacity: 0; transform: translateY(-8px); }\n    to { opacity: 1; transform: translateY(0); }\n}\n\n@keyframes spin {\n    to { transform: rotate(360deg); }\n}\n\n/* â”€â”€â”€ Scrollbar â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€â”€ */\n::-webkit-scrollbar {\n    width: 6px;\n    height: 6px;\n}\n::-webkit-scrollbar-track {\n    background: transparent;\n}\n::-webkit-scrollbar-thumb {\n    background: var(--card-border);\n    border-radius: 3px;\n}\n::-webkit-scrollbar-thumb:hover {\n    background: var(--text-secondary);\n}", "",{"version":3,"sources":["webpack://./src/taskpane/taskpane.css"],"names":[],"mappings":"AAAA;;;;EAIE;;AAEF;IACI,mBAAmB;IACnB,sBAAsB;IACtB,kBAAkB;IAClB,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,kBAAkB;IAClB,wBAAwB;IACxB,qBAAqB;IACrB,uBAAuB;IACvB,yBAAyB;IACzB,kBAAkB;IAClB,qBAAqB;IACrB,gBAAgB;IAChB,mBAAmB;IACnB,aAAa;IACb,4CAA4C;IAC5C,uFAAuF;IACvF,iDAAiD;AACrD;;AAEA;IACI;QACI,mBAA